var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"about-us-wrapper container"},[_c('div',{staticClass:"text-container page"},[_c('p',{staticClass:"text"},[_vm._v("\n        Looking back, a decade had passed since Bitcoin, the first\n        cryptocurrency and blockchain, the technology behind Bitcoin and other\n        cryptocurrencies were first introduced to the world. These\n        revolutionary technologies have definitely taken the world by surprise\n        with its cutting-edge technology and its industry disrupting\n        potential. Despite the surge in the number of cryptocurrencies listed\n        on Coinmarketcap and the market capitalization reaching over $300\n        billion USD, cryptocurrencies are still used in a few places\n        worldwide. The adoption of cryptocurrency is still primarily\n        concentrated among a relatively small subset of people despite all the\n        efforts in bringing it to the mainstream.\n      ")]),_c('br'),_c('p',{staticClass:"text"},[_vm._v("\n        While we noticed these issues, there’s a bigger issue that we would\n        like to address. Notice how the world around you has changed. Looking\n        at old photos of your parents or your grandparents, notice how those\n        places look then and now. It might be that the big tree you used to\n        spend swinging from when you were a child wasn’t there anymore. When\n        you step outside of your old house, notice how the landscape has\n        changed. Notice how easily people get sick, how certain places easily\n        get flooded, how animals are losing their homes, and how water\n        shortage becoming a global phenomenon. All these occurrences and more\n        are manifestations of the degradation of our environment and the\n        effects of climate change due to global warming.\n      ")]),_c('br'),_c('p',{staticClass:"text"},[_vm._v("\n        You might not be able to experience these things at hand for now but\n        if you look around on the internet, magazines, radios, and\n        televisions, you will see these different manifestations of climate\n        change in different places around the world. We are now living in a\n        critical time for both the planet and humanity.\n      ")]),_c('br'),_c('p',{staticClass:"text"},[_vm._v("\n        Coinserves saw these problems and would like to invite you to help us\n        solve them. As far as we are concerned about promoting these\n        life-changing revolutionary technologies into the world and promote\n        mass adoption, we are also taking into consideration the effects that\n        technological advancements have in the environment. As crypto\n        enthusiasts, we would like to help in spreading the word about\n        blockchain and cryptocurrencies and how it will change and improve the\n        current flawed financial system that we have. We are even more\n        delighted to do it in a way that would serve both humanity and the\n        planet.\n      ")]),_c('br'),_c('p',{staticClass:"text"},[_vm._v("\n        The first solution that we propose is through the planting of trees\n        based on the corresponding number of crypto transactions that\n        registered users can make. We will do this by tracking each\n        transaction that users will have the moment they signed up to us using\n        their active BTC wallet address. Through this, we will encourage the\n        crypto community to do more crypto transactions (peer to peer\n        transfer, merchant payment, etc.) and to encourage non-crypto users to\n        use cryptocurrencies instead of fiat currencies.\n      ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"our-founders"},[_c('div',{staticClass:"our-founders-gray"}),_c('div',{staticClass:"container",staticStyle:{"position":"relative"}},[_c('div',{staticStyle:{"font-size":"calc(2vw + 25px)","color":"white","font-weight":"bold","text-align":"center"}},[_vm._v("\n        OUR FOUNDERS\n      ")]),_c('div',{staticClass:"text-container",staticStyle:{"margin-top":"calc(2vw + 30px)","margin-bottom":"calc(2vw + 30px)"}},[_c('p',{staticClass:"text text-white"},[_vm._v("\n          Coinserves was co-founded by a couple, Karen and Dave. Both are in\n          their twenties, with a passion for technology, especially with\n          blockchain and cryptocurrency. Both graduated from Notre Dame of\n          Dadiangas University in the Philippines and came from the same\n          province.\n        ")]),_c('br'),_c('br'),_c('br'),_c('br'),_c('p',{staticClass:"text text-white"},[_vm._v("\n          Karen graduated magna cum laude with a degree in Pharmacy and is now\n          a licensed Pharmacist in the Philippines. Currently, she’s not\n          practicing her profession and immersed herself in the FinTech\n          industry to support his partner, Dave. As a healthcare practitioner,\n          she really strived hard to learn about blockchain and cryptocurrency\n          and wanted to take part in the next evolutionary step towards the\n          FinTech industry. Despite coming from a different field, she was\n          able to adapt and was able to start a new venture with Dave and\n          established Coinserves.\n        ")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"our-challenge"},[_c('div',{staticClass:"our-challenge-white"}),_c('div',{staticClass:"container",staticStyle:{"position":"relative"}},[_c('div',{staticClass:"text-container",staticStyle:{"font-size":"calc(2vw + 25px)","font-weight":"bold","margin-top":"calc(2vw + 30px)"}},[_vm._v("\n        OUR CHALLENGE\n      ")]),_c('div',{staticClass:"text-container",staticStyle:{"margin-bottom":"calc(2vw + 30px)","margin-top":"calc(2vw + 30px)"}},[_c('p',{staticClass:"text"},[_vm._v("\n          Even before blockchain and cryptocurrencies have been discovered,\n          humans and previous technological discoveries have already hurt and\n          damaged our environment and that is a fact. Technological\n          advancements simultaneously help us in one way but hurt as in\n          another. But there are plenty of ways that we can do in order to\n          lessen these effects. It’s not too late for us to show our love and\n          care for our planet.\n        ")]),_c('br'),_c('br'),_c('br'),_c('p',{staticClass:"text"},[_vm._v("\n          Let us all take this opportunity to educate everyone regarding the\n          usefulness and advantages of cryptocurrencies and how it disrupts\n          the current financial system and at the same time, educate them\n          about the importance of saving our environment.\n        ")]),_c('br'),_c('br'),_c('br'),_c('p',{staticClass:"text"},[_vm._v("\n          Our financial freedom is worth fighting for. Our planet is worth\n          fighting for. Humanity is always worth fighting for.\n        ")])])])])}]

